html[data-dark="false"] {
  color-scheme: light;
  --primary: #0b7a75;
  --secondary: #e8f4f2;
  --text: #0f172a;
  --background: #edf2f7;
  --background-alt: #e7eef6;
  --section-base: #ecf2f8;
  --section-alt: #e4ebf3;
  --light-gray: #d2ddeb;
  --gray: #5f6f85;
  --dark-gray: #2f3d52;
  --overlay: #0f172a29;
  --surface: #f9fcff;
  --surface-alt: #f3f8fd;
  --surface-frost: #f5f9ffbf;
  --frame-frost: #edf4fca8;
  --header-frost: #edf4fc82;
  --surface-veil: #edf3fbbf;
  --border: #c9d5e3;
  --glass-highlight: #ffffffbf;
  --focus: #0b7a7540;
  --primary-soft: #0b7a7528;
  --bg-glow-1: #0b7a750d;
  --bg-glow-2: #2563eb0b;
  --media-image-opacity: 0.34;
  --media-veil-opacity: 0.68;
  --banner-image-opacity: 0.56;
  --banner-veil-opacity: 0.3;
  --header-banner-image-opacity: 0.64;
  --header-banner-veil-opacity: 0.2;
  --header-ink: #0c1628;
  --footer-ink: #132035;
  --header-chip: #f0f5fcc9;
  --header-chip-border: #c8d5e6d9;
  --header-text-shadow: #ffffff52;
  --tooltip-bg: #101828;
  --tooltip-text: #f8fafc;
  --tooltip-border: #1f2937;
}

html[data-dark="true"] {
  color-scheme: dark;
  --primary: #48d0be;
  --secondary: #1a2d32;
  --text: #e7edf5;
  --background: #0d131b;
  --background-alt: #121a24;
  --section-base: #0f161f;
  --section-alt: #131c28;
  --light-gray: #2a384b;
  --gray: #9aa8bc;
  --dark-gray: #c8d5e6;
  --overlay: #02060c70;
  --surface: #16202c;
  --surface-alt: #1b2735;
  --surface-frost: #16202ca3;
  --frame-frost: #16202c82;
  --header-frost: #16202c59;
  --surface-veil: #16202ca8;
  --border: #2a3a4d;
  --glass-highlight: #ffffff1f;
  --focus: #48d0be45;
  --primary-soft: #48d0be2b;
  --bg-glow-1: #48d0be1a;
  --bg-glow-2: #60a5fa12;
  --media-image-opacity: 0.28;
  --media-veil-opacity: 0.58;
  --banner-image-opacity: 0.5;
  --banner-veil-opacity: 0.26;
  --header-banner-image-opacity: 0.58;
  --header-banner-veil-opacity: 0.18;
  --header-ink: #f2f7ff;
  --footer-ink: #e7edf5;
  --header-chip: #0f1a2788;
  --header-chip-border: #344860cc;
  --header-text-shadow: #00000070;
  --tooltip-bg: #182331;
  --tooltip-text: #e7edf5;
  --tooltip-border: #2a3a4d;
}

:root {
  --title: "Plus Jakarta Sans", sans-serif;
  --heading: "Plus Jakarta Sans", sans-serif;
  --body: "Source Sans 3", sans-serif;
  --code: "JetBrains Mono", monospace;
  --large: 1.25rem;
  --xl: 1.55rem;
  --xxl: 2.2rem;
  --thin: 200;
  --regular: 400;
  --semi-bold: 600;
  --bold: 700;
  --spacing: 1.75;
  --compact: 1.45;
  --rounded-sm: 10px;
  --rounded: 14px;
  --rounded-lg: 18px;
  --shadow: 0 14px 36px -28px var(--overlay);
  --shadow-strong: 0 26px 56px -34px var(--overlay);
  --transition: 220ms cubic-bezier(0.22, 1, 0.36, 1);
  --page: 1080px;
  --measure: 74ch;
  --measure-wide: 88ch;
  --section-pad: clamp(26px, 4vw, 60px);
  --tooltip-bg-light: #ffffff;
  --tooltip-text-light: #0f172a;
  --tooltip-border-light: #d6e0ec;
  --tooltip-bg-dark: #101828;
  --tooltip-text-dark: #f8fafc;
  --tooltip-border-dark: #1f2937;
}
